/* Variables */
/* Mixins */
/* Global Styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1440px;
  background-color: #FFFFFF;
}

/* Image Styles */
img {
  width: 100%;
  height: auto;
  max-height: 350px;
  margin: 0 auto;
}

/* Main Content Styles */
h1 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.15rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
}

.desc {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 23.75em;
  height: 6em;
  margin-bottom: 2rem;
}

.btn, #btn-reset {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  width: 8rem;
  height: 3rem;
  background-color: #E4C1AE;
  border-radius: 3rem;
  border: none;
  color: #FFFFFF;
  cursor: pointer;
}

/* Modal Styles */
.modal {
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  width: 90%;
  height: 280px;
  background-color: #FFFFFF;
  opacity: 1;
  margin: 15rem auto;
  padding-left: 3rem;
  animation-name: animateOpacity;
  animation-duration: 0.5s;
}
.modal-content .close {
  width: 1.5rem;
  height: 1.5rem;
  float: right;
  font-size: 1.5rem;
  margin-top: 2.875rem;
  margin-right: 2.875rem;
  cursor: pointer;
}
.modal-content h2 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.15rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
  padding-top: 3.5rem;
}
.modal-content p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: "Roboto", sans-serif;
  color: #121212;
  margin-bottom: 1.5rem;
}

#btn-reset {
  position: relative;
  font-size: 0.9rem;
  color: #121212;
  opacity: 0;
}

@keyframes animateOpacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Layout Styles */
.container {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
}

/* Media Queries */
@media screen and (min-width: 600px) {
  .modal-content {
    width: 70%;
    height: 280px;
  }
}
@media screen and (min-width: 880px) {
  .container {
    grid-template-columns: 1.3fr 1fr;
    justify-content: center;
    margin-top: 6rem;
  }
  .container .main-content {
    align-self: center;
  }
  .container .main-content .title {
    margin-top: 0;
  }
  img {
    width: 100%;
    height: 300px;
  }
}
@media screen and (min-width: 998px) {
  img {
    width: 505px;
    height: 330px;
  }
}
@media screen and (min-width: 1100px) {
  .container {
    grid-template-columns: 505px 380px;
    grid-template-rows: 1fr;
    gap: 96px;
  }
  .modal-content {
    width: 920px;
    height: 210px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    margin: 0;
  }
  #btn-reset {
    left: 35rem;
    bottom: 3.75rem;
  }
}